<template>
  <div class="default-container">
    <Row :gutter="20">
      <Col span="8">
        <Card class="no-title">
          <Table :columns="todayColumns" :data="dealTodayClasses"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
      <Col span="8">
        <Card class="no-title">
          <Table :columns="subjectColumns" :data="getSubjects"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
      <Col span="8">
        <Card class="no-title">
          <Table :columns="classesColumns" :data="getClasses"></Table>
        </Card>
        <!-- eslint-disable -->
      </Col>
    </Row>
    <div class="echart-line">
      <div ref="dateEcharts"></div>
    </div>
  </div>
</template>

<script>
import time from '../mixins/time'
export default {
  mixins: [time],
  data () {
    return {
      data: [],
      studentsMap: {},
      today: '',
      todayClasses: [],
      todayColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '姓名',
        key: 'studentName',
        align: 'center'
      }, {
        title: '上课时间',
        key: 'periods',
        align: 'center'
      }, {
        title: '小时数',
        key: 'count',
        align: 'center'
      }],
      subjectColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '科目',
        key: 'label',
        align: 'center'
      }, {
        title: '学生数量',
        key: 'value',
        align: 'center'
      }],
      classesColumns: [{
        title: '序号',
        type: 'index',
        width: 100,
        align: 'center'
      }, {
        title: '科目',
        key: 'label',
        align: 'center'
      }, {
        title: '课时数量',
        key: 'value',
        align: 'center'
      }],
      subjectMap: { read: 0, hearing: 0, writing: 0, speaking: 0 }

    }
  },
  computed: {
    dealTodayClasses () {
      console.log(this.dealAllData(this.todayClasses))
      return this.dealAllData(this.todayClasses)
    },
    getSubjects () {
      let data = []
      let obj = { ...this.subjectMap }
      for (const s of this.studentsData) {
        ++obj[s.subjects]
      }
      for (const k in obj) {
        data.push({
          label: this.subjectMaps[k],
          value: obj[k]
        })
      }
      return data
    },
    getClasses () {
      let data = []
      let obj = { ...this.subjectMap }
      for (const d of this.data) {
        let key = ''
        for (const s of this.studentsData) {
          if (s.id === Number(d.studentId)) {
            key = s.subjects
          }
        }
        obj[key] += 0.5
      }
      for (const k in obj) {
        data.push({
          label: this.subjectMaps[k],
          value: obj[k]
        })
      }
      return data
    }
  },
  created () {
    this.today = this.$lib.myMoment(new Date()).formate('YYYY-MM-DD')
    this.$indexedDB.initStore('curriculum', this.query)
  },
  mounted () {
  },
  methods: {
    getTodayClasses () {
      this.todayClasses = this.data.filter(item => {
        return item.date === this.today
      })
    },
    query () {
      this.$indexedDB.http({
        method: 'get',
        url: 'curriculum'
      }).then((res) => {
        this.data = res.data.filter(item => {
          return !item.isDelect
        })
        this.getTodayClasses()
      })
    }
  }
}
</script>

